@import url('https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300;400;500;600;700;800;900&family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Quicksand:wght@300;400;500;600;700&family=Sora:wght@100;200;300;400;500;600;700;800&display=swap');

::-webkit-scrollbar {
    display: none;
}

.main_sidebar {
    height: 100vh;
    background-color: white;
    padding-left: 25px;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    overflow: scroll;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding-bottom: 40px;

    .sidebar_title {
        font-size: 25px;
        font-family: "Poppins", sans-serif;
        font-weight: 700;
    }

    .input_group {
        display: flex;
        flex-direction: column;
        padding-top: 20px;
        font-family: "Poppins", sans-serif;

        .label {
            font-size: 16px;
            font-family: "Poppins", sans-serif;
            font-weight: 500;
            padding-bottom: 10px;
        }


        .textAreagroup {
            display: flex;
            flex-direction: column;
            padding-top: 30px;

            .textTitlw {
                font-size: 16px;
                font-family: "Poppins", sans-serif;
                font-weight: 500;
                padding-bottom: 10px;
            }

            textarea {
                width: 90%;
                height: 150px;
                background-color: #ffffff;
                border: 1px solid #d9d9d9;
                transition: all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
                border-radius: 8px;
                font-size: 14;
                font-family: "Poppins", sans-serif;
                font-weight: 500;
                padding-bottom: 10px;
                padding-left: 20px;
                resize: none;
                outline: none;
                padding-top: 10px;
            }

            .ant-btn-dashed {
                height: 40px !important;
                font-family: "Poppins", sans-serif !important;
                font-weight: 600 !important;
                font-size: 14px !important;
                width: 90%;
                margin-top: 30px;
                border-color: red;
                color: red;
            }

            .note {
                background-color: rgba(255, 227, 18);
                width: 90%;
                height: 40px;
                font-family: "Poppins", sans-serif;
                font-weight: 600;
                font-size: 14px;
                margin-top: 30px;
                border-radius: 8px;
                display: flex;
                align-items: center;
                padding-left: 30px;

            }

        }

        .ant-tag-has-color {
            background-color: black;
            border-radius: 8px;

        }

        .ant-select-selector {
            height: 42px;
        }

        .ant-tag {
            font-size: 14px;
            font-family: "Poppins", sans-serif;
            font-weight: 500;
        }


        .ant-select-selection-item {
            display: flex;
            align-items: center;
            font-size: 14px !important;
            font-family: "Poppins", sans-serif !important;
            font-weight: 500 !important;
        }

        .ant-select-item-option-content {
            line-height: 40px !important;
            font-size: 14px !important;
            font-family: "Poppins", sans-serif !important;
            font-weight: 500 !important;
        }

        .ant-slider {
            font-size: 15px !important
        }

        .main_slide {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-right: 15px;
            gap: 10px;
            font-family: "Poppins", sans-serif;
            font-weight: 600;

            .ant-slider {
                width: 100% !important;
            }

        }

        .ant-checkbox-wrapper {
            span {
                font-family: "Poppins", sans-serif;
                font-weight: 500;
                font-size: 15px;
            }
        }


    }

    .btn_sec {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        padding-top: 30px;
        padding-right: 25px;
.cutom_btn1{
    border-radius: 30px !important;
    background-color: black !important;
}
.cutom_btn2{
    border-radius: 30px !important;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: red !important;
}
.cutom_btn3{
    border-radius: 30px !important;
    background-color: red !important;
}
        .route {
            background-color: #08979c;
            width: 100%;
            height: 40px;
            font-family: "Poppins", sans-serif;
            font-weight: 600;
            font-size: 14px;
            border-radius: 8px;
            display: flex;
            color: white;
            cursor: pointer;
            align-items: center;
            justify-content: center;

        }

        .ant-btn {
            height: 40px !important;
            font-family: "Poppins", sans-serif !important;
            font-weight: 500 !important;
            font-size: 14px !important;
        }
        .antd-btn {
            height: 40px !important;
            font-family: "Poppins", sans-serif !important;
            font-weight: 500 !important;
            font-size: 14px !important;
            color: #fff;
            background-color: #4096ff;
            padding: 4px 15px;
            border-radius: 6px;
            width: 100%;
            display: flex;
            text-align: center;
            align-items: center;
            justify-content: center;
            gap: .5rem;
        }

    }
}