.main_upload {

    height: 100vh;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
 





.upload_content{
    display: flex;
    justify-content: center;
    align-items: center;
flex-direction: column;
height: 100vh;
.icon_left{
    top: 30px;
    left: 30px;
    position: absolute;
    background-color: gray;
    color: white;
    font-size: 20px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;

}

    .file-upload-card {
        border: 1px dashed #08979c;
        width: 400px;
        height: 330px;
        border-radius: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
    
        .file-name {
            font-size: 16px;
            font-family: "Poppins", sans-serif;
            font-weight: 500;
        }
    
        .box-icon {
            display: flex;
            flex-direction: column;
            align-items: center;
    
            .icon {
                font-size: 70px;
                color: #08979c;
            }
    
            .box-title {
                font-size: 16px;
                font-family: "Poppins", sans-serif;
                font-weight: 500;
            }
        }
    }
    .upload {
        margin-top: 30px;
        background-color: #08979c;
        width: 400px;
        height: 40px;
        font-family: "Poppins", sans-serif;
        font-weight: 600;
        font-size: 14px;
        border-radius: 8px;
        display: flex;
        color: white;
        cursor: pointer;
        align-items: center;
        justify-content: center;
    &:hover{
    background-color: #87e8de;
    }
    }
}

}