.ant-empty-description{
    font-size: 15px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
}

.main_body {
    height: 100vh;
    overflow: scroll;
    display: flex;
    justify-content: center;

    .body_content {
        width: 80%;
        padding-top: 100px;

        .body_title {
            font-size: 25px;
            font-family: "Poppins", sans-serif;
            font-weight: 700;
            padding-bottom: 30px;
        }

        .name {
            padding-bottom: 30px;
            font-size: 15px;
            font-family: "Poppins", sans-serif;
            font-weight: 500;
            display: flex;
            gap: 1rem;
        }

        .main_paras {
            display: flex;
            flex-direction: column;
            gap: 2.2rem;
            padding-bottom: 50px;

            .resulted_para {

                .number {
                    font-size: 25px;
                    font-family: "Poppins", sans-serif;
                    font-weight: 700;
                }

                .para {
                    font-size: 16px;
                    font-family: "Poppins", sans-serif;
                    font-weight: 500;
                    margin-bottom: 40px;
                    padding-top: 30px;
                    width: 100%;
                    background-color: unset;
                    border: none;
                    resize: unset;
                }

                .editable-para {
                    font-size: 16px;
                    font-family: "Poppins", sans-serif;
                    font-weight: 500;
                    padding-bottom: 40px;
                    padding-top: 30px;
                    width: 100%;
                    padding-left: 20px;
                    resize: none;
                    margin-bottom: 20px;
                    margin-top: 20px;
                }

                .btn_sec {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                }
            }
        }

        .save {
            display: flex;
            justify-content: flex-end;
            width: 100%;
            align-items: flex-end;
            padding-bottom: 50px;
        }

    }

}